.header {
  background: #fff;
  border-bottom: 2px solid #0af;
  padding: 20px 0;
  font-family: Segoe UI, sans-serif;
}

.header > .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  color: #000;
  font-family: Impact;
  font-size: 24px;
  font-weight: 800;
  font-style: Regular;
  leading-trim: NONE;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  margin: 0;
  font-size: 32px;
  line-height: 32px;
}

.header-logo .green {
  color: #2bb673;
}

.header__list {
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__item {
  font-family: Poppins;
  font-weight: 300;
  font-style: Light;
  letter-spacing: .5px;
  font-size: 16px;
  line-height: 24px;
}

.header__item:hover {
  color: #0af;
}

.header__burger {
  cursor: pointer;
  background: none;
  border: none;
  flex-direction: column;
  gap: 6px;
  display: none;
}

.header__burger span {
  background-color: #000;
  width: 24px;
  height: 2px;
  display: block;
}

@media (width <= 760px) {
  .header > .container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  .header__burger {
    display: flex;
  }

  .header__list {
    z-index: 100;
    background-color: #fff;
    border: 1px solid #0af;
    border-radius: 8px;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
  }

  .header__list.active {
    display: flex;
  }
}

.hero {
  padding-top: 184px;
}

.face {
  display: none;

  @media screen and (width >= 1280px) {
    width: 108px;
    margin-left: 40px;
    display: inline;
  }
}

.hero__headline {
  color: var(--Black, #292929);
  letter-spacing: 1.8px;
  text-transform: uppercase;
  font-family: Impact;
  font-size: 136px;
  font-style: normal;
  font-weight: 400;
  line-height: 156px;

  @media screen and (width >= 360px) {
    width: 320px;
  }

  @media screen and (width >= 768px) {
    width: 704px;
  }

  @media screen and (width >= 1280px) {
    width: auto;
  }
}

.hero__link {
  color: var(--Black, #292929);
  letter-spacing: .5px;
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 24px;
  transition: color .5s;
}

.hero__link:hover {
  color: var(--Accet-green, #00b068);
}

.hero__flex-box-center {
  position: relative;

  @media screen and (width >= 360px) {
    margin-bottom: 72px;
  }

  @media screen and (width >= 768px) {
    margin-bottom: 80px;
  }

  @media screen and (width >= 1280px) {
    justify-content: space-between;
    margin-bottom: 132px;
    display: flex;
  }
}

.hero__rectangle {
  background: var(--Light-accent, #bcdfd1);
  z-index: -5;
  border-radius: 16px;
  flex-shrink: 0;
  position: absolute;
  right: -80px;

  @media screen and (width >= 360px) {
    flex-shrink: 0;
    width: 165px;
    height: 92px;
  }

  @media screen and (width >= 768px) {
    width: 261px;
    height: 96px;
    right: -160px;
  }
}

.hero__text {
  color: var(--Black, #292929);
  letter-spacing: .5px;
  text-transform: uppercase;
  width: 592px;
  font-family: Poppins;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

.arrow__right {
  width: 28px;
  fill: var(--Accet-green, #00b068);
  margin-right: 12px;
}

.hero__links-list {
  background-color: #d3d3d3;
  background: var(--Light-grey, #e4e5e6);
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
}

.hero__headline-part2 {
  letter-spacing: 1.8px;
  text-transform: uppercase;
  font-family: Impact;
  font-size: 136px;
  font-style: normal;
  font-weight: 400;
  line-height: 156px;

  @media screen and (width >= 360px) {
    width: 320px;
    margin-bottom: 72px;
  }

  @media screen and (width >= 768px) {
    width: 704px;
    margin-bottom: 92px;
  }

  @media screen and (width >= 1280px) {
    margin-bottom: 80px;
  }
}

.hero__order {
  align-items: center;
  display: flex;
  position: relative;
}

.hero__order-button {
  color: var(--Black, #292929);
  letter-spacing: .5px;
  text-transform: uppercase;
  z-index: 2;
  cursor: pointer;
  background-color: #0000;
  border: none;
  align-items: center;
  margin-left: auto;
  font-family: Poppins;
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
  display: flex;
  position: relative;

  @media screen and (width >= 360px) {
    margin-bottom: 40px;
  }

  @media screen and (width >= 768px) {
    margin-bottom: 40px;
    margin-left: 0;
  }

  @media screen and (width >= 1280px) {
    margin-bottom: 0;
  }
}

.hero__up-decor {
  position: absolute;

  @media screen and (width >= 360px) {
    width: 351px;
    top: 150px;
    right: -280px;
  }

  @media screen and (width >= 768px) {
    width: 356px;
    top: 230px;
    right: 60px;
  }

  @media screen and (width >= 1280px) {
    top: 130px;
    right: 350px;
  }
}

.hero__down-decor {
  position: absolute;

  @media screen and (width >= 360px) {
    width: 169px;
    top: 70px;
    left: 250px;
  }

  @media screen and (width >= 768px) {
    width: 356px;
    top: -93px;
    left: 170px;
  }

  @media screen and (width >= 1280px) {
    width: 356px;
    top: -85px;
    left: 150px;
  }
}

.aboutme {
  color: #000;
  background: #fff;
  padding: 60px 0;
  font-family: Segoe UI, sans-serif;
}

.aboutme__maintitle {
  text-transform: uppercase;
  font-family: Impact;
  font-size: 36px;
  font-weight: 400;
  font-style: Regular;
  letter-spacing: 1.8px;
  text-transform: uppercase;
  grid-column: 1;
  margin: 0 0 20px;
  font-size: 108px;
  line-height: 124px;
}

.aboutme__maintitle:after {
  content: " ME";
  color: #2bb673;
}

.aboutme__tittle, .aboutme__textone, .aboutme__texttwo {
  grid-column: 2;
}

.aboutme__tittle {
  margin: 0 0 16px;
  font-size: 24px;
  font-weight: 600;
}

.aboutme__textone, .aboutme__texttwo {
  margin: 0 0 16px;
  font-size: 16px;
  line-height: 1.6;
}

.abotme__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: contents;
}

.aboutme__item:first-child {
  grid-column: 1;
}

.aboutme__item:nth-child(2) {
  grid-column: 2;
}

.aboutme__item {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.aboutme__minitittle {
  text-transform: uppercase;
  font-family: Poppins;
  font-size: 20px;
  font-weight: 500;
  font-style: Medium;
  leading-trim: NONE;
  letter-spacing: .5px;
  text-transform: uppercase;
  margin: 0 0 8px;
  font-size: 36px;
  line-height: 48px;
}

.aboutme__text {
  angle: 0 deg;
  opacity: 1;
  gap: 8px;
  width: 384px;
  height: 48px;
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
}

.aboutme__item {
  flex-direction: column;
  gap: 8px;
  display: flex;
  transform: translateY(10px);
}

.aboutme > .container {
  grid-template-columns: 1fr 1fr;
  align-items: start;
  gap: 20px 40px;
  display: grid;
}

.aboutme > .container .aboutme__img {
  object-fit: cover;
  border-radius: 8px;
  grid-column: 1 / -1;
  justify-self: start;
  width: 280px;
  height: auto;
  margin-top: -250px;
  margin-bottom: 0;
  transform: translateX(-12px)translateY(-40px);
  box-shadow: 0 0 10px #0000001a;
}

.aboutme__img {
  object-fit: cover;
  border-radius: 8px;
  grid-column: 1 / -1;
  width: 280px;
  height: auto;
  position: relative;
  top: -60px;
  left: 200px;
  box-shadow: 0 0 10px #0000001a;
}

.aboutme > .container .abotme__list {
  display: contents;
}

.aboutme > .container .aboutme__item:first-child {
  grid-column: 1;
  position: relative;
  top: -64px;
  left: 190px;
}

.aboutme > .container .aboutme__item:nth-child(2) {
  grid-column: 2;
  position: relative;
  top: -64px;
}

.aboutme {
  padding-top: 40px;
  padding-bottom: 48px;
}

.aboutme > .container .aboutme__tittle {
  margin-top: -6px;
}

.aboutme > .container .aboutme__textone, .aboutme > .container .aboutme__texttwo {
  margin-bottom: 14px;
}

.aboutme__texttwo, .aboutme__textone, .aboutme__text {
  font-family: Poppins;
  font-weight: 300;
  font-style: Light;
  letter-spacing: .5px;
  font-size: 16px;
  line-height: 24px;
}

@media (width <= 600px) {
  .aboutme {
    padding: 32px 16px;
  }

  .aboutme > .container {
    flex-direction: column;
    align-items: stretch;
    gap: 24px;
    display: flex;
  }

  .aboutme__maintitle {
    letter-spacing: 1.2px;
    text-align: left;
    margin: 0;
    font-size: 64px;
    line-height: 72px;
  }

  .aboutme__maintitle:after {
    content: " ME";
    color: #2bb673;
  }

  .aboutme__tittle {
    text-align: left;
    margin: 0;
    font-size: 20px;
    font-weight: 600;
  }

  .aboutme__textone, .aboutme__texttwo {
    text-align: left;
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
  }

  .aboutme__img {
    object-fit: cover;
    border-radius: 8px;
    width: 100%;
    max-width: 280px;
    height: auto;
    margin: 0 auto;
    bottom: 100px;
    transform: none;
    box-shadow: 0 0 10px #0000001a;
  }

  .abotme__list {
    flex-direction: column;
    gap: 32px;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .aboutme__item {
    flex-direction: column;
    gap: 8px;
    display: flex;
    position: static;
    transform: none;
  }

  .aboutme__minitittle {
    text-transform: uppercase;
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
  }

  .aboutme__text {
    margin: 0;
    font-size: 16px;
    line-height: 24px;
  }
}

